import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Form, Col, Checkbox, Table, message } from 'antd';
import { CartHeadAction } from 'components/common/index';
import { getQueryParams } from 'utils/urlUtil';
import {
  getStudentField,
  getStudentWithFields,
  exportStudentWithFields,
} from 'services/exportStudentsByField';
import BtnExport from 'components/common/button/BtnExport';
import { calculateWidthOfTextOnScreen } from 'utils/functionUtil';
import SearchBar from './SearchBar';
import './style.scss';

@Form.create({ name: 'formListSubsidyObject' })
class ListSubsidyObject extends Component {
  state = {
    loadingFields: false,
    loadingStudents: false,
    paramSearch: {},
    dataFields: [],
    dataStudent: [],
    columnsField: [],
    columnsStudent: [],
    totalXOfFields: 0,
    totalXOfFilter: 0,
  };
  
  componentDidMount = async () => {
    this.handleChangeQueryParams();
    this.setState({
      loadingFields: true,
    });
    const res = await getStudentField();
    let totalXOfFields = 0;
    const columnsField = res.data.map( item => {
      const columnObj = {
        title: item.label,
        dataIndex: item.field,
        key: item.field,
      };
      const tempWidth = calculateWidthOfTextOnScreen(item.label);
      columnObj.width = tempWidth < 100 ? 100 : tempWidth + 20;
      totalXOfFields += columnObj.width;
      columnObj.render = (text, record) => <Checkbox checked={record[item.field]} onChange={() => this.handleFieldsChange(item.field)} />;
      return columnObj;
    });
    const dataFields = [{
      customIdForThis: Math.floor(Math.random() * 999999) + 1,
    }];
    res.data.forEach(item => {
      dataFields[0][item.field] = item.fieldChecked;
    });
    this.setState({
      columnsField,
      dataFields,
      totalXOfFields,
      loadingFields: false,
    });
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = async () => {
    const { columnsStudent } = this.state;
    if(columnsStudent.length) {
      this.setState({
        loadingStudents: true,
      });
      const paramSearch = getQueryParams();
      paramSearch.fieldList = columnsStudent.map(item => ({
        field: item.key, 
        label: item.title, 
        fieldChecked: true
      }));
      const res = await getStudentWithFields(paramSearch);
      this.setState({
        paramSearch,
        dataStudent: res.data.studentList,
        loadingStudents: false,
      });
    } else {
      message.warn('Vui lng chn ct d liu ca sinh vin cn xut!');
    }
  };

  handleFieldsChange = (field) => {
    const { dataFields, columnsField, columnsStudent } = this.state;
    dataFields[0][field] = !dataFields[0][field];
    const isExist = columnsStudent.some(item => item.key === field);
    let tempColumnsStudent = [...columnsStudent];
    if(isExist) {
      tempColumnsStudent = tempColumnsStudent.filter(item => item.key !== field);  
    } else {
      const oneColumn = columnsField.filter(item => item.key === field);
      tempColumnsStudent.push({...oneColumn[0]});
    }
    let totalXOfFilter = 0;
    tempColumnsStudent.forEach(item => { 
      totalXOfFilter += item.width;
      delete item.render;
    });
    this.setState({
      dataFields,
      totalXOfFilter,
      columnsStudent: tempColumnsStudent,
    });
  };

  handleExport = async () => {
    const { paramSearch } = this.state;
    await exportStudentWithFields(paramSearch);
  };

  render() {
    const { 
      columnsField, 
      columnsStudent,
      dataFields, 
      loadingFields, 
      loadingStudents,
      totalXOfFields, 
      totalXOfFilter, 
      dataStudent,
    } = this.state;
    return (
      <Fragment>
        <Card title="Chn i tng chnh sch">
          <CartHeadAction>
            <BtnExport
              title="Xut Excel"
              onClick={this.handleExport}
            />
          </CartHeadAction>
          <Row>
            <Col span={24}>
              <SearchBar />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table
                bordered
                loading={loadingFields}
                dataSource={dataFields}
                columns={columnsField}
                scroll={{ x: totalXOfFields, y: 300 }}
                rowKey="customIdForThis"
              />
            </Col>
          </Row>
          <Row>
            <Col span={24}>
              <Table
                bordered
                loading={loadingStudents}
                dataSource={dataStudent}
                columns={columnsStudent}
                scroll={{ x: totalXOfFilter, y: 300 }}
                rowKey="customIdForThis"
              />
            </Col>
          </Row>
        </Card>
      </Fragment>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListSubsidyObject />
  </Page>
);



